// 防抖
export const debounce = function(func, wait = 150) {
  let timer = 0
  let self = this
  return function(...args) {
    if (timer) {
      // 如果在指定时间间隔内又再次触发，则继续延时
      clearTimeout(timer)
    }
    // 重点：使用$nextTick函数
    self.$nextTick(() => {
      timer = setTimeout(() => {
        func.apply(self, args)
      }, wait)
    })
  }
}

export const resize = function() {
  return debounce.call(this, function() {
    //
    let fixedHeight = 204
    if (this.$refs.toolsBar) {
      fixedHeight += 14
    }
    this.tableOpts.height =
      window.innerHeight -
      this.$refs.searchDiv.$el.offsetHeight -
      fixedHeight -
      this.$refs.toolsBar.$el.offsetHeight
  })
}
