Vue.config.productionTip = false

Vue.prototype.$resize = resize

router.beforeEach((to, from, next) => {
  ViewUI.LoadingBar.start()
  if (Cookie.get('token')) {
    if (to.path !== 'login') {
      store.dispatch('addTag', to)
    }
    next()
  } else {
    if (to.path == '/login') {
      next()
    } else {
      next({
        path: '/login',
        query: { redirect: to.path }
      })
    }
  }
})

router.afterEach(() => {
  ViewUI.LoadingBar.finish()
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
